.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    width: 100%;
    
}

.about__me-image{
    transform: rotate(0deg);
    transition: var(--transition);
}

.about__me-image:hover{
    transform: rotate(10deg);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.9rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}


/* MEDIA QUERIES (MEDIUM DEVIVES) */
@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    p {
        margin: 1rem 0 1.5rem;
    }
}

/* MEDIA QUERIES (SMALL DEVIVES) */
@media screen and (max-width: 600px) {
    .about__me{
        width: 80%;
        margin: 0 auto 3rem;
        
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__contents{
        text-align: center;
    }

    p{
        margin: 1.5rem 0;
    }

.about__h5{
    margin-top: -3rem;
}

.about__h2{
    margin-top: -0.2rem;
}
}