header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}



/* CTA */
.cta{
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* HEADER SOCIALS */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    bottom: 5rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* IMAGE ME */
.me{
    width: 25rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

/* SCROLL DOWN */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-size: 0.9rem;
}


/* MEDIA QUERIES (MEDIUM DEVIVES) */
@media screen and (max-width: 1024px) {
    header{
        height: 68vh;
    }
}

/* MEDIA QUERIES (SMALL DEVIVES) */
@media screen and (max-width: 600px) {

    header{
        height: 70vh ;
    }

    .header__socials, .scroll__down{
        display:none;
    }

   
}